*{
  font-family: Helvetica, Arial, sans-serif;
}
body{
  padding: 20px;
}

input{
  color: grey;
  width: 100%;
  font-size: 1.2rem;
  padding: 10px;
  border-radius: 10px;
  border: 1px solid #000;
}

.card-grid{
  display: flex;
  flex-wrap: wrap;

}

.card-item{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.7);
  margin-right: 20px;
  margin-bottom: 20px;
  overflow-x: hidden;
  max-width: 300px;
}
.card-item img{
  max-height: 200px;
  max-width: 2000px;
  object-fit: cover;
}
.card-item p{
  color: #111;
  margin-top: 20px;
}